import React, {Component} from 'react';
import './App.css';
import Typography from "@material-ui/core/Typography/Typography";
import withStyles from "@material-ui/core/styles/withStyles";
import Grid from "@material-ui/core/Grid/Grid";
import Input from "@material-ui/core/Input/Input";
import Table from "@material-ui/core/Table/Table";
import TableRow from "@material-ui/core/TableRow/TableRow";
import TableBody from "@material-ui/core/TableBody/TableBody";
import TableCell from "@material-ui/core/TableCell/TableCell";
import Checkbox from "@material-ui/core/Checkbox/Checkbox";
import EventIcon from "@material-ui/icons/Event";
import RadioUncheckedIcon from "@material-ui/icons/RadioButtonUnchecked";
import RadioCheckedIcon from "@material-ui/icons/RadioButtonChecked";
import ClearButton from "@material-ui/icons/Clear";
import classnames from 'classnames';

import TodoFilters from './TodoFIlters';
import IconButton from "@material-ui/core/IconButton/IconButton";
import Button from "@material-ui/core/Button/Button";

const styles = () => ({
    title: {
        fontFamily: 'cursive',
        color: '#e98074',
        padding: '40px 0 32px 0',
        textShadow: '0.07em 0.02em #8E8D8A',
    },
    card: {
        background: '#D8c3a5',
        boxShadow: '0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 11px 15px 0 rgba(0, 0, 0, 0.2) ',
        padding: '1rem',
    },
    icon: {
        color: 'rgba(0,0,0,0.42)',
        transition: 'color 0.2s cubic-bezier(0.4, 0, 0.2, 1)',
        marginRight: '1rem',
        order: -1,
        '&:hover': {
            color: 'rgba(0,0,0,0.87)',
        },
    },
    inputRoot: {
        flexGrow: 1,
        fontFamily: 'serif',
    },
    inputFocused: {
        '&::after': {
            borderColor: '#ff4081',
            transition: 'all 0s',
        },
        '& ~ $icon': {
            color: '#ff4081',
        }
    },
    inputUnderline: {
        '&::after': {
            transition: 'all 0s',
        },
    },
    message: {
        color: '#e08074',
    },
    table: {
        marginTop: '1rem',
    },
    tableRow: {
        height: '3.5rem',
        '&:hover $tableCellClearIcon': {
            display: 'table-cell'
        }
    },
    tableCell: {
        borderBottom: 0
    },
    tableCellCompleted: {
        textDecoration: 'line-through',
        color: '#8c8c8c',
    },
    tableCellIcon: {
        width: '10%'
    },
    tableCellClearIcon: {
        display: 'none'
    },
    footRow: {
        height: '1rem',
    },
    leftItems: {
        color: 'rgba(0,0,0,0.38)',
        fontFamily: 'serif',
        '-webkitFontSmoothing': 'antialiased',
    },
    button: {
        color: '#e98074',
        fontFamily: 'serif',
        transitionDuration: '0.2s',
        margin: '5px',
        minWidth: '5.5rem',
        '-webkitFontSmoothing': 'antialiased',
        '&:hover': {
            color: 'rgba(0,0,0,0.87)',
        }
    },
    selectedButton: {
        color: 'black',
        background: 'rgb(189, 171, 148)',
    },
    clearButton: {
        fontWeight: '400',
        textTransform: 'none',
        '&:hover': {
            textDecoration: 'underline',
            backgroundColor: 'transparent',
        }
    }
});

let cnt = 1;

class App extends Component {
    state = {
        message: '',
        content: '',
        todos: [],
        filter: TodoFilters.ALL,
    };

    componentDidMount() {
        if (localStorage.todoState) {
            this.setState(JSON.parse(localStorage.todoState))
        }
    }

    componentDidUpdate() {
        localStorage.todoState = JSON.stringify(this.state);
    }

    handleClick = id => {
        this.setState({
            todos: this.state.todos.map(todo => todo.id === id ? {...todo, completed: !todo.completed} : todo)
        })
    };

    handleChange = e => {
        this.setState({
            content: e.target.value,
        })
    };

    handleClickDelete = (id, e) => {
        e.stopPropagation();
        this.setState({
            todos: this.state.todos.filter(todo => todo.id !== id)
        })
    };

    handleKeyPress = e => {
        if (e.charCode === 13) {
            const content = this.state.content.trim();
            if (content) {
                this.setState({
                    todos: this.state.todos.concat({
                        id: cnt++,
                        content: content,
                        completed: false,
                    }),
                    content: '',
                })
            }
        }
    };

    handleFilterClick = filter => {
        this.setState({
            filter
        })
    };
    handleClickClear = e => {
        this.setState({
            todos: this.state.todos.filter(todo => !todo.completed),
        })
    };

    render() {
        const {classes} = this.props;
        const {todos, message, filter} = this.state;
        const leftItems = todos.length;
        const visibleTodos = todos.filter(todo => filter === TodoFilters.ALL ? true : filter === TodoFilters.COMPLETED ? todo.completed : !todo.completed);
        const showClearCompletedTodos = todos.some(todo => todo.completed);
        return (
            <div className="App">
                <Typography variant='display3' className={classes.title}>
                    TO DO LIST
                </Typography>
                <Grid container justify='center' className={classes.message}>
                    {message}
                </Grid>
                <Grid container justify='center'>
                    <Grid item container xs={6} className={classes.card}>
                        <Input placeholder="Write a to do item"
                               classes={{
                                   root: classes.inputRoot,
                                   focused: classes.inputFocused,
                                   underline: classes.inputUnderline
                               }}
                               onKeyPress={this.handleKeyPress}
                               onChange={this.handleChange}
                               value={this.state.content}
                        />
                        <EventIcon className={classes.icon}/>
                        <Table className={classes.table}>
                            <TableBody>
                                {visibleTodos.map(todo => {
                                    return (
                                        <TableRow hover onClick={this.handleClick.bind(this, todo.id)} key={todo.id}
                                                  className={classes.tableRow}>
                                            <TableCell padding='checkbox'
                                                       className={classnames(classes.tableCell, classes.tableCellIcon)}>
                                                <Checkbox icon={<RadioUncheckedIcon/>} checkedIcon={<RadioCheckedIcon/>}
                                                          checked={todo.completed}/>
                                            </TableCell>
                                            <TableCell
                                                className={classnames(classes.tableCell,
                                                    {[classes.tableCellCompleted]: todo.completed}
                                                )}>
                                                {todo.content}
                                            </TableCell>
                                            <TableCell
                                                className={classnames(classes.tableCell, classes.tableCellIcon, classes.tableCellClearIcon)}>
                                                <IconButton onClick={this.handleClickDelete.bind(this, todo.id)}>
                                                    <ClearButton/>
                                                </IconButton>
                                            </TableCell>
                                        </TableRow>
                                    )
                                })}
                            </TableBody>
                        </Table>
                        <Grid container item xs={2} alignItems='center'>
                            <Typography
                                className={classes.leftItems}>Left items: {leftItems}</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            {Object.keys(TodoFilters).map(key => {
                                return (
                                    <Button
                                        className={classnames(classes.button, {[classes.selectedButton]: filter === TodoFilters[key]})}
                                        onClick={this.handleFilterClick.bind(this, key)}
                                        key={key}>{TodoFilters[key]}</Button>
                                )
                            })}
                        </Grid>
                        <Grid container item xs={2} alignItems='center'>
                            {showClearCompletedTodos ?
                                <Button className={classnames(classes.clearButton, classes.leftItems)} size='small'
                                        onClick={this.handleClickClear}>
                                    Clear Completed
                                </Button> : null}
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default withStyles(styles)(App);



